<template>
    <div v-if="mode == 'home'" class="container h-100 d-flex flex-column justify-content-between">
        <div class="banner-top-container w-100">
            <BannerTop :title="title_mode" />
        </div>

        <div class="">
            <div class="m-2 p-2 d-flex flex-column align-items-center">
                <p class="text-center m-1">Qui commence ?</p>
                <div class="btn-group-vertical" role="group">
                    <button type="button" class="btn" :class="{'btn-dark': p_start=='me', 'btn-outline-dark': p_start!='me'}" @click="click_p_start('me')">Joueur 1 (Naruto)</button>
                    <button type="button" class="btn" :class="{'btn-dark': p_start=='opp', 'btn-outline-dark': p_start!='opp'}" @click="click_p_start('opp')">Joueur 2 (Sasuke)</button>
                </div>
            </div>

            <div class="m-2 p-2 d-flex flex-column align-items-center">
                <p class="text-center m-1">Le joueur qui commence utilise :</p>
                <div class="btn-group" role="group">
                    <button type="button" class="btn" :class="{'btn-dark': p_start_c=='clock', 'btn-outline-dark': p_start_c!='clock'}" @click="click_p_start_c('clock')">
                        <Arrow />
                        <p class="m-0">sens horaire</p>
                    </button>
                    <button type="button" class="btn" :class="{'btn-dark': p_start_c=='c-clock', 'btn-outline-dark': p_start_c!='c-clock'}" @click="click_p_start_c('c-clock')">
                        <Arrow :is_clockwise="false" />
                        <p class="m-0">sens antihoraire</p>
                    </button>
                </div>
            </div>

            <div class="m-2 p-2 d-flex flex-column align-items-center">
                <p class="text-center m-1">Vitesse des cailloux</p>
                <div class="btn-group" role="group">
                    <button type="button" class="btn" :class="{'btn-dark': move_speed=='slow', 'btn-outline-dark': move_speed!='slow'}" @click="click_speed('slow')">Lente</button>
                    <button type="button" class="btn" :class="{'btn-dark': move_speed=='normal', 'btn-outline-dark': move_speed!='normal'}" @click="click_speed('normal')">Moyenne</button>
                    <button type="button" class="btn" :class="{'btn-dark': move_speed=='fast', 'btn-outline-dark': move_speed!='fast'}" @click="click_speed('fast')">Rapide</button>
                </div>
            </div>

            <div class="m-3 d-flex flex-column align-items-center">
                <button class="btn btn-block btn-outline-dark" @click="start_game()"><i class="fa-solid fa-person-running"></i> Commencer</button>
                <router-link to="/" class="btn btn-block btn-outline-dark"><i class="fa-solid fa-rotate-left"></i> Accueil</router-link>
            </div>
        </div>

        <div></div>
    </div>
    <div v-else class="container h-100 d-flex flex-column justify-content-around">
        <div class="banner-top-container w-100">
            <BannerTop :title="title_mode" />
        </div>

        <div class="board-extend-container">
            <div class="row h-100">
                <div class="col h-100">
                    <BoardExtend :speed_timer="speed_timer" :p_start="p_start" :p_start_c="p_start_c" :opp_name="'Sasuke'" :me_name="'Naruto'" />
                </div>
            </div>
        </div>

        <div class="row">
            <div class="col d-flex justify-content-center">
                <ButtonConfig @speed="update_speed" @retry="retry_game" :speed="move_speed" />
            </div>
        </div>

        <div></div>
    </div>
</template>

<script>
// import axios from 'axios'
import BoardExtend from '@/components/game/BoardExtend.vue'
import BannerTop from '@/components/game/BannerTop.vue'
import ButtonConfig from '@/components/game/ButtonConfig.vue'
import Arrow from '@/components/game/Arrow.vue'

export default {
    components: {
        BoardExtend,
        BannerTop,
        ButtonConfig,
        Arrow
    },
    data () {
        return {
            speed_timer: 400,
            // speed_timer: 4000,
            move_speed: 'normal',
            mode: 'home',
            p_start: 'me',
            p_start_c: 'clock',
            title_mode: "Multijoueur en local"
        }
    },
    methods: {
        update_speed(speed) {
            this.speed_timer = speed
            let speed_update = 400
            if (speed == 400) {
                speed_update = 'normal'
            }
            else if (speed == 1200) {
                speed_update = 'slow'
            }
            else if (speed == 90) {
                speed_update = 'fast'
            }
            this.move_speed = speed_update
        },
        click_p_start(who) {
            this.p_start = who
        },
        click_p_start_c(clock) {
            this.p_start_c = clock
        },
        start_game() {
            this.mode = 'game'
        },
        retry_game(value) {
            if (value) {
                this.mode = 'home'
            }
        },
        click_speed(speed) {
            this.move_speed = speed
            let speed_update = 400
            if (speed == 'normal') {
                speed_update = 400
            }
            else if (speed == 'slow') {
                speed_update = 1200
            }
            else if (speed == 'fast') {
                speed_update = 90
            }
            this.speed_timer = speed_update
        }
    },
    mounted() {
        // this.$store.dispatch('set_token', "changed!")
    }
}
</script>

<style scoped>
/* .banner-top-container {
    height: 15%;
} */
.board-extend-container {
    height: 75%;
}
</style>