<template>
    <div class="h-100 d-flex flex-column justify-content-between">
        <div class="dashboard-player">
            <DashBoardPlayer 
                :is_clockwise="p_start == 'opp' && p_start_c == 'clock' || p_start == 'me' && p_start_c == 'c-clock'" 
                :player_name="opp_name"
                :is_turn="who_turn == 'opp'"
                :pts="opp_pts"
                />
        </div>

        <div class="board-container">
            <div class="row h-100">
                <div class="col h-100">
                    <Board 
                        @turn="update_turn" 
                        @pts="update_pts"
                         @winner="end_game" 
                         :speed_timer="speed_timer" 
                         :who_turn="p_start" 
                         :direction_me="p_start == 'me' && p_start_c == 'clock' || p_start == 'opp' && p_start_c == 'c-clock' ? 'clock' : 'c-clock'"
                         :opp_name="opp_name"
                         :me_name="me_name" 
                         :mode="mode"
                         :other_sid="other_sid"
                         :socket="socket"
                         :game_code="game_code"
                         :sid="sid"
                         :is_host="is_host"
                         />
                </div>
            </div>
        </div>

        <div class="dashboard-player">
            <DashBoardPlayer 
                :is_clockwise="p_start == 'me' && p_start_c == 'clock' || p_start == 'opp' && p_start_c == 'c-clock'" 
                :player_name="me_name"
                :is_turn="who_turn == 'me'"
                :pts="me_pts"
                />
        </div>
    </div>
</template>

<script>
// import axios from 'axios'
import Board from '@/components/game/Board.vue'
import DashBoardPlayer from '@/components/game/DashBoardPlayer.vue'

export default {
    components: {
        Board,
        DashBoardPlayer
    },
    props: {
        p_start: {
            type: String,
            default: "me"
        },
        p_start_c: {
            type: String,
            default: "clock"
        },
        speed_timer: {
            tyoe: Number,
            default: 300
        },
        opp_name: {
            type: String,
            default: "Joueur 2"
        },
        me_name: {
            type: String,
            default: "Joueur 1"
        },
        mode: {
            type: String,
            default: "local"
        },
        other_sid: {
            type: String,
            default: ''
        },
        socket: {
            default: null
        },
        game_code: {
            type: String,
            default: ''
        },
        sid: {
            type: String,
            default: ''
        },
        is_host: {
            type: Boolean,
            default: true
        }
    },
    data () {
        return {
            who_turn: this.p_start,
            opp_pts: 0,
            me_pts: 0,
            winner: 'none'
        }
    },
    methods: {
        update_turn(who) {
            this.who_turn = who
            this.$emit("turn", who)
        },
        update_pts(pts) {
            this.opp_pts = pts.opp
            this.me_pts = pts.me
        },
        end_game(winner) {
            this.winner = winner
        }
    },
    created() {
        this.$store.dispatch('set_is_in_game', true)
    },
    beforeUnmount() {
        this.$store.dispatch('set_is_in_game', false)
    }
}
</script>

<style scoped>
.board-container {
    height: 74%;
}

.dashboard-player {
    height: 12%;
}
</style>