<template>
    <button class="btn btn-sm btn-outline-dark" data-toggle="modal" data-target="#menu-game-modal" ref="btn_menu" @click="open_modal()">
        <i class="bi bi-gear"></i>
        Paramètre
    </button>

    <!-- Modal -->
    <div class="modal fade" id="menu-game-modal" tabindex="-1" role="dialog" aria-labelledby="menu-game-modal-title" aria-hidden="true">
        <div class="modal-dialog modal-dialog-centered" role="document">
            <div class="modal-content">
                <div class="modal-header">
                    <h5 class="modal-title">Paramètre</h5>
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                        <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div class="modal-body">
                    <div class="d-flex flex-column align-items-center">
                        <p class="m-0 mb-2 text-center">Vitesse</p>
                        <div class="btn-group" role="group">
                            <button type="button" class="btn" :class="{'btn-dark': speed=='slow', 'btn-outline-dark': speed!='slow'}" @click="click_speed('slow')" :disabled="is_speed_btn_disabled">Lente</button>
                            <button type="button" class="btn" :class="{'btn-dark': speed=='normal', 'btn-outline-dark': speed!='normal'}" @click="click_speed('normal')" :disabled="is_speed_btn_disabled">Moyenne</button>
                            <button type="button" class="btn" :class="{'btn-dark': speed=='fast', 'btn-outline-dark': speed!='fast'}" @click="click_speed('fast')" :disabled="is_speed_btn_disabled">Rapide</button>
                        </div>
                        <p v-if="mode == 'online' && !is_host" class="my-2 text-center font-italic text-danger">Seul l'hôte peut changer la vitesse et recommencer la partie</p>
                        <p v-if="mode == 'online' && is_host && turn != 'me'" class="m-0 text-center font-italic text-danger">Attendez que l'adversaire finisse son tour avant de changer la vitesse</p>
                    </div>

                    <div class="mt-3">
                        <div v-if="mode != 'online' || (mode == 'online' && is_host)" class="mt-3 d-flex justify-content-center">
                            <button v-if="!wanna_retry" class="btn btn-block btn-outline-dark" @click="try_retry()">Recommencer</button>
                            <div v-else class="">
                                <p v-if="mode != 'online'" class="text-center m-1">Revoir les configurations et recommencer ?</p>

                                <div v-if="mode == 'online'" class="d-flex flex-column align-items-center">
                                    <p class="m-0 mb-2 text-center">Qui commence ?</p>
                                    <div class="btn-group-vertical" role="group">
                                        <button type="button" class="btn btn-sm" :class="{'btn-dark': p_s=='me', 'btn-outline-dark': p_s!='me'}" @click="click_p_start('me')">Vous</button>
                                        <button type="button" class="btn btn-sm" :class="{'btn-dark': p_s=='opp', 'btn-outline-dark': p_s!='opp'}" @click="click_p_start('opp')">L'adversaire</button>
                                    </div>
                                </div>

                                <div v-if="mode == 'online'" class="m-1 p-2 d-flex flex-column align-items-center">
                                    <p class="text-center m-1">Le joueur qui commence utilise :</p>
                                    <div class="btn-group" role="group">
                                        <button type="button" class="btn btn-sm" :class="{'btn-dark': p_s_c=='clock', 'btn-outline-dark': p_s_c!='clock'}" @click="click_p_start_c('clock')">
                                            <Arrow />
                                            <p class="m-0">sens horaire</p>
                                        </button>
                                        <button type="button" class="btn btn-sm" :class="{'btn-dark': p_s_c=='c-clock', 'btn-outline-dark': p_s_c!='c-clock'}" @click="click_p_start_c('c-clock')">
                                            <Arrow :is_clockwise="false" />
                                            <p class="m-0">sens antihoraire</p>
                                        </button>
                                    </div>
                                </div>

                                <div class="d-flex justify-content-around">
                                    <button class="btn btn-sm btn-outline-dark m-2" @click="retry(false)">Annuler</button>
                                    <button class="btn btn-sm btn-outline-dark m-2" @click="retry(true)">Recommencer</button>
                                </div>
                            </div>
                        </div>
                        <div class="mt-3 d-flex justify-content-center">
                            <button v-if="!wanna_quit" class="btn btn-block btn-outline-dark" @click="try_quit()">{{mode == "online" ? "Quitter" : "Accueil"}}</button>
                            <div v-else class="">
                                <p class="text-center m-1">Quitter et aller à l'accueil ?</p>
                                <div class="d-flex justify-content-around">
                                    <button class="btn btn-sm btn-outline-dark m-2" @click="quit(false)">Non</button>
                                    <button class="btn btn-sm btn-outline-dark m-2" @click="quit(true)">Oui</button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import Arrow from '@/components/game/Arrow.vue'

export default {
    components: {
        Arrow
    },
    props: {
        speed: {
            type: String,
            default: "normal"
        },
        mode: {
            type: String,
            default: "local"
        },
        is_host: {
            type: Boolean,
            default: true
        },
        turn: {
            type: String,
            default: "me"
        },
        winner: {
            type: String,
            default: "none"
        },
        p_start: {
            type: String,
            default: "me"
        },
        p_start_c: {
            type: String,
            default: "clock"
        }
    },
    emits: ["speed", "retry", "update_p_c"],
    data () {
        return {
            move_speed: this.speed,
            wanna_retry: false,
            wanna_quit: false,
            p_s: this.p_start,
            p_s_c: this.p_start_c
        }
    },
    computed: {
        is_speed_btn_disabled() {
            if (!this.is_host) {
                return true
            }
            if (this.mode == "online" && this.is_host && this.turn != "me") {
                return true
            }
            return false
        }
    },
    methods: {
        click_p_start(who) {
            this.p_s = who
        },
        click_p_start_c(who) {
            this.p_s_c = who
        },
        click_speed(speed) {
            let speed_update = 400
            if (speed == 'normal') {
                speed_update = 400
            }
            else if (speed == 'slow') {
                speed_update = 1200
            }
            else if (speed == 'fast') {
                speed_update = 90
            }

            this.$emit('speed', speed_update)
        },
        try_retry() {
            this.wanna_retry = true
        },
        retry(is_retry) {
            this.wanna_retry = is_retry
            if (is_retry) {
                if (this.mode == "online") {
                    this.trigger_close_modal()
                    this.$emit("update_p_c", {
                        "p_s": this.p_s,
                        "p_s_c": this.p_s_c
                    })
                }
                this.$emit("retry", true)
            }
        },
        open_modal() {
            this.wanna_retry = false
            this.wanna_quit = false
        },
        trigger_close_modal() {
            if (document.querySelectorAll('.modal.show#menu-game-modal').length > 0) {
                this.$refs.btn_menu.click()
            }
        },
        try_quit() {
            this.wanna_quit = true
        },
        quit(is_quit) {
            this.wanna_quit = is_quit
            if (is_quit) {
                this.trigger_close_modal()
                this.$router.push({path: '/'})
            }
        },
    },
    beforeUnmount() {
        this.trigger_close_modal()
    }
}
</script>

