import { createStore } from 'vuex'

export default createStore({
  state: {
    token: "loading",
    // url_b: 'http://' + window.location.hostname + ':5000',
    // url_b: 'http://192.168.88.10:5000',
    // url_b: 'http://192.168.88.4:5000',
    // url_b: 'https://katro.backend.inonary.com',
    url_b: 'https://boggy-candy-spoon.glitch.me',
    is_in_game: false
  },
  mutations: {
    SET_TOKEN(state, payload) {
      state.token = payload
    },
    SET_IS_IN_GAME(state, payload) {
      state.is_in_game = payload
    }
  },
  actions: {
    set_token(context, token) {
      context.commit('SET_TOKEN', token)
    },
    set_is_in_game(context, is_in) {
      context.commit('SET_IS_IN_GAME', is_in)
    }
  },
  modules: {
  }
})
