<template>
    <div class="board h-100 d-flex flex-column justify-content-around" ref="board">
        <div class="board-side" ref="board_side_1">

            <div v-if="winner != 'none'" class="banner-winner">
                <p class="m-0 text-center p-2 font-1">{{the_winner_is}}</p>
            </div>



            <div v-for="board_row in 2" :key="board_row" class="h-50 row board-row d-flex align-items-center">
                <div v-for="row_hole in 4" :key="row_hole" class="p-1" :style="{'width': hole_size, 'height': hole_size}">
                    <div class="hole h-100 w-100" :id="'hole_opp'+((board_row - 1) * 4 + row_hole)" :class="{'can_hover_opp': can_hover_opp && turn == 'opp', 'focused_hole': focused_hole_moving == 'opp'+((board_row - 1) * 4 + row_hole)}" @click="click_hole('opp', (board_row - 1) * 4 + row_hole)">
                        <PebblesContainer :v="hole_layer_1['opp'][(board_row - 1) * 4 + row_hole]" :top="0" :left="0" :size="100" :zindex="10" :layer="1" />
                        <PebblesContainer :v="delt_move['opp'][(board_row - 1) * 4 + row_hole]" :id="'pc_opp'+((board_row - 1) * 4 + row_hole)" :top="hole_pos['opp'][(board_row - 1) * 4 + row_hole].top" :left="hole_pos['opp'][(board_row - 1) * 4 + row_hole].left" :size="100" :zindex="11" :layer="2" :hole_size="hole_size" />
                        <!-- {{ pebbles["opp"][(board_row - 1) * 4 + row_hole] }} -->
                    </div>
                </div>
            </div>
        </div>

        <div class="board-side">
            <div v-for="board_row in 2" :key="board_row" class="h-50 row board-row d-flex align-items-center">
                <div v-for="row_hole in 4" :key="row_hole" class="p-1" :style="{'width': hole_size, 'height': hole_size}">
                    <div class="hole h-100 w-100" :id="'hole_me'+((board_row - 1) * 4 + row_hole)" :class="{'can_hover_me': can_hover_me && turn == 'me', 'focused_hole': focused_hole_moving == 'me'+((board_row - 1) * 4 + row_hole)}" @click="click_hole('me', (board_row - 1) * 4 + row_hole)">
                        <PebblesContainer :v="hole_layer_1['me'][(board_row - 1) * 4 + row_hole]" :top="0" :left="0" :size="100" :zindex="10" :layer="1" />
                        <PebblesContainer :v="delt_move['me'][(board_row - 1) * 4 + row_hole]" :id="'pc_me'+((board_row - 1) * 4 + row_hole)" :top="hole_pos['me'][(board_row - 1) * 4 + row_hole].top" :left="hole_pos['me'][(board_row - 1) * 4 + row_hole].left" :size="100" :zindex="11" :layer="2" :hole_size="hole_size" />
                        <!-- {{ pebbles["me"][(board_row - 1) * 4 + row_hole] }} -->
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import PebblesContainer from '@/components/game/PebblesContainer.vue'
import $ from 'jquery'

export default {
    components: {
        PebblesContainer
    },
    props: {
        who_turn: {
            type: String,
            default: 'me'
        },
        speed_timer: {
            type: Number,
            default: 300
        },
        direction_me: {
            type: String,
            default: 'clock'
        },
        opp_name: {
            type: String,
            default: "Joueur 2"
        },
        me_name: {
            type: String,
            default: "Joueur 1"
        },
        mode: {
            type: String,
            default: "local"
        },
        other_sid: {
            type: String,
            default: ''
        },
        socket: {
            default: null
        },
        game_code: {
            type: String,
            default: ''
        },
        sid: {
            type: String,
            default: ''
        },
        is_host: {
            type: Boolean,
            default: true
        }
    },
    data () {
        return {
            hole_size: '0px',
            pebbles: {
                "opp": {},
                "me": {}
            },
            turn: this.who_turn,
            can_hover_opp: true,
            can_hover_me: true,
            direction: this.direction_me,
            can_I_click: true,
            can_opp_click: true,
            focused_hole_moving: null,
            timer: null,
            winner: 'none',
            hole_pos: {
                "opp": {},
                "me": {}
            },
            timer_move: null,
            tmstp_end_ms: 0,
            time_step_move_ms: 4000,
            in_hand: 0,
            delt_move: {
                "opp": {},
                "me": {}
            },
            are_pebbles_moving: false,
            hole_layer_1: {
                "opp": {},
                "me": {}
            },
            opp_has_finished_turn_socket: false,
            opp_has_finished_turn_animation: false
        }
    },
    computed: {
        the_winner_is() {
            let winner = "Joueur"
            if (this.winner == 'opp') {
                winner = this.opp_name
            }
            else if (this.winner == 'me') {
                winner = this.me_name
            }
            return winner + " a gagné !"
        }
    },
    methods: {
        update_hole_size() {
            let length = this.$refs.board_side_1.clientHeight
            let width = this.$refs.board_side_1.clientWidth
            let size = 0
            if (width > length * 2) {
                size = length / 2
            }
            else {
                size = width / 4
            }
            this.hole_size = size + 'px'
        },
        init_pos_pc() {
            for (let i = 1; i <= 8; i++) {
                this.hole_pos["opp"][i].top = this.hole_pos["opp"][i].top == 0 ? 0.1 : 0
                this.hole_pos["opp"][i].left = this.hole_pos["opp"][i].left == 0 ? 0.1 : 0
                this.hole_pos["me"][i].top = this.hole_pos["me"][i].top == 0 ? 0.1 : 0
                this.hole_pos["me"][i].left = this.hole_pos["me"][i].left == 0 ? 0.1 : 0
            }
        },
        init_layer_1() {
            for (let i = 1; i <= 8; i++) {
                this.hole_layer_1["opp"][i] = 0
                this.hole_layer_1["me"][i] = 0
            }
        },
        animate_hole(who, num, to_who, to_num, return_pc = false) {
            this.are_pebbles_moving = true

            const rect_from = document.getElementById('pc_'+who+num).getBoundingClientRect()
            const rect_to = document.getElementById('hole_'+to_who+to_num).getBoundingClientRect()
            const delta_x = rect_to.left - rect_from.left
            const delta_y = rect_to.top - rect_from.top

            const add_x = delta_x > 0 ? '+='+delta_x+'px' : '-='+(-delta_x)+'px'
            const add_y = delta_y > 0 ? '+='+delta_y+'px' : '-='+(-delta_y)+'px'

            // Changer un peu la position du hole_pos pour prendre en compte la mise à jour du component enfant
            this.hole_pos[who][num].left = 0.1
            this.hole_pos[who][num].top = 0.1

            const self = this

            $('#pc_'+who+num).animate({
                left: add_x,
                top: add_y
            }, this.speed_timer, 'swing', function(){
                const rect_hole_from = document.getElementById('hole_'+who+num).getBoundingClientRect()
                const rect_hole_to = document.getElementById('hole_'+to_who+to_num).getBoundingClientRect()

                const delta_x_up = rect_hole_to.left - rect_hole_from.left
                const delta_y_up = rect_hole_to.top - rect_hole_from.top

                self.hole_pos[who][num].left = delta_x_up
                self.hole_pos[who][num].top = delta_y_up

                if (return_pc) {
                    self.hole_pos[who][num].left = self.hole_pos[who][num].left == 0 ? 0.1 : 0
                    self.hole_pos[who][num].top = self.hole_pos[who][num].top == 0 ? 0.1 : 0
                    // self.delt_move[who][num] = self.pebbles[who][num]
                    self.delt_move[who][num] = 0
                    self.delt_move[to_who][to_num] = 0
                }
                
                self.are_pebbles_moving = false
            })
        },
        get_next_num(who, num) {
            if (who == "me" && this.direction == 'clock' || who == 'opp' && this.direction == 'c-clock') {
                if (num == 4) {
                    num = 8
                }
                else if (num == 5) {
                    num = 1
                }
                else if (num > 4) {
                    num -= 1
                }
                else {
                    num += 1
                }
            }
            else {
                if (num == 8) {
                    num = 4
                }
                else if (num == 1) {
                    num = 5
                }
                else if (num > 4) {
                    num += 1
                }
                else {
                    num -= 1
                }
            }
            return num
        },
        has_pebbles_front(who = "opp") {
            let h_from = 5
            let h_to = 8
            
            if (who == "me") {
                h_from = 1
                h_to = 4
            }

            for (let i = h_from; i <= h_to; i++) {
                if (this.pebbles[who][i] > 0) {

                    return true
                }
            }

            return false
        },
        get_position_pebbles_and_score() {
            let pos = {
                "pos": {
                    "host": {},
                    "client": {}
                },
                "score": {
                    "host": 0,
                    "client": 0
                }
            }

            for (let i = 1; i <= 8; i++) {
                let j = i
                if (!this.is_host) {
                    j = 9 - i
                    pos.score.host += this.pebbles["opp"][j]
                    pos.score.client += this.pebbles["me"][j]

                    pos.pos.host[j] = this.pebbles["opp"][i]
                    pos.pos.client[j] = this.pebbles["me"][i]
                }
                else {
                    pos.score.host += this.pebbles["me"][j]
                    pos.score.client += this.pebbles["opp"][j]

                    pos.pos.host[j] = this.pebbles["me"][j]
                    pos.pos.client[j] = this.pebbles["opp"][j]
                }
            }

            return pos
        },
        move_pebbles(who, num) {
            this.make_turn('none')
            this.init_pos_pc()
            this.init_layer_1()
            this.focused_hole_moving = who + num
            this.in_hand = 0
            var starting = true
            var has_got_from_other = false

            const num_traveler = num
            this.animate_hole(who, num_traveler, who, this.get_next_num(who, num_traveler))

            this.delt_move[who][num] = this.pebbles[who][num]

            this.opp_has_finished_turn_socket = false
            this.opp_has_finished_turn_animation = false

            this.timer = setInterval(function() { 
                if (this.are_pebbles_moving) {
                    return false
                }

                if (!starting && this.in_hand <= 0 && this.pebbles[who][num] == 1) {
                    clearInterval(this.timer)
                    this.focused_hole_moving = null
                    this.init_pos_pc()
                    this.init_layer_1()
                    this.hole_pos[who][num_traveler].left = 0
                    this.hole_pos[who][num_traveler].top = 0
                    this.delt_move[who][num_traveler] = this.pebbles[who][num_traveler]
                    if (this.mode == "online" && this.turn == "me") {
                        this.make_turn("switch")
                        let data = {
                            "to": this.other_sid,
                            "from": this.sid,
                            "game_code": this.game_code,
                            "pos": this.get_position_pebbles_and_score()
                        }
                        this.socket.emit("finish_turn", JSON.stringify(data))
                    }
                    else if (this.mode == "online" && this.turn == "opp") {
                        if (this.opp_has_finished_turn_socket) {
                            this.make_turn("switch")
                        }
                        else {
                            this.opp_has_finished_turn_animation = true
                        }
                    }
                    else if (this.mode != "online") {
                        this.make_turn('switch')
                    }
                    return false
                }
                else if (this.in_hand <= 0 && this.pebbles[who][num] > 1) {
                    
                    if (num == num_traveler) {
                        this.hole_layer_1[who][num_traveler] = 0
                    }

                    var from_other = 0
                    var other_id = ''
                    var num_other = null
                    if (who == 'me' && num >= 1 && num <= 4) {
                        other_id = 'opp'
                        num_other = num + 4
                        from_other = this.pebbles[other_id][num_other]
                        if (from_other <= 0 && !this.has_pebbles_front(other_id)) {
                            num_other = num
                            from_other = this.pebbles[other_id][num_other]
                        }
                    }
                    else if (who == 'opp' && num >= 5 && num <= 8) {
                        other_id = 'me'
                        num_other = num - 4
                        from_other = this.pebbles[other_id][num_other]
                        if (from_other <= 0 && !this.has_pebbles_front(other_id)) {
                            num_other = num
                            from_other = this.pebbles[other_id][num_other]
                        }
                    }

                    if (!starting && !has_got_from_other && from_other > 0) {
                        this.pebbles[who][num] += from_other
                        this.delt_move[who][num_traveler] = this.pebbles[who][num]
                        this.pebbles[other_id][num_other] = 0
                        has_got_from_other = true
                        this.emit_pts()
                        if (this.check_winner() != 'none') {
                            clearInterval(this.timer)
                            this.focused_hole_moving = null
                            this.emit_winner()

                            // Save result to server
                            if (this.is_host) {
                                let data = {
                                    "game_code": this.game_code,
                                    "pos": this.get_position_pebbles_and_score(),
                                    "winner": this.winner == "me" ? "host" : "client"
                                }
                                this.socket.emit("game_over", JSON.stringify(data))
                            }
                        }
                        else {
                            this.animate_hole(who, num_traveler, who, this.get_next_num(who, num))
                        }
                        return false
                    }

                    this.in_hand = this.pebbles[who][num]
                    this.pebbles[who][num] = 0
                }
                else if (starting && this.pebbles[who][num] == 1) {
                    this.in_hand = this.pebbles[who][num]
                    this.pebbles[who][num] = 0
                }

                starting = false
                has_got_from_other = false

                num = this.get_next_num(who, num) 

                this.focused_hole_moving = who + num

                this.pebbles[who][num] += 1
                this.in_hand -= 1
                this.delt_move[who][num_traveler] -= 1

                if (num != num_traveler) {
                    this.delt_move[who][num] = this.pebbles[who][num]
                }
                else {
                    this.hole_layer_1[who][num_traveler] = this.pebbles[who][num_traveler]
                }

                if (this.in_hand > 0) {
                    this.animate_hole(who, num_traveler, who, this.get_next_num(who, num))
                } 
                else {
                    if (this.pebbles[who][num] > 1) {
                        // Checking if gain opp's pebbles
                        if (who == "me" && num > 4 || who == "opp" && num < 5) {
                            // NOT GAINING ! MUST CONTINUE
                            this.delt_move[who][num_traveler] = this.pebbles[who][num]
                            this.delt_move[who][num] = 0
                            this.animate_hole(who, num_traveler, who, this.get_next_num(who, num))
                        }
                        else {
                            let who_other = "opp"
                            if (who == 'opp') {
                                who_other = 'me'
                            }
                            
                            let nb_pebbles_other = 0
                            let num_other = 1

                            if (who == 'me') {
                                num_other = num + 4
                                nb_pebbles_other = this.pebbles[who_other][num_other]
                                if (nb_pebbles_other <= 0 && !this.has_pebbles_front(who_other)) {
                                    num_other = num
                                }
                            }
                            else {
                                num_other = num - 4
                                nb_pebbles_other = this.pebbles[who_other][num_other]
                                if (nb_pebbles_other <= 0 && !this.has_pebbles_front(who_other)) {
                                    num_other = num
                                }
                            }
                            nb_pebbles_other = this.pebbles[who_other][num_other]

                            this.delt_move[who][num_traveler] = this.pebbles[who][num]
                            if (nb_pebbles_other > 0) {
                                this.animate_hole(who_other, num_other, who, num, true)
                            }
                            else {
                                this.delt_move[who][num] = 0
                                this.animate_hole(who, num_traveler, who, this.get_next_num(who, num))
                            }
                        }
                    }
                }
            }.bind(this), this.speed_timer / 10)

        },
        click_hole(who, num) {
            if (who == 'me' && this.can_I_click || who == 'opp' && this.can_opp_click) {
                if (this.pebbles[who][num] > 0) {
                    this.move_pebbles(who, num)
                    if (this.mode == "online" && who == "me" && this.socket) {
                        let data = {
                            to: this.other_sid,
                            num: num,
                            game_code: this.game_code,
                            speed: this.speed_timer
                        }
                        this.socket.emit('move_pebbles', JSON.stringify(data))
                    } 
                }
            }
        },
        make_turn(who) {
            if (who == 'switch') {
                if (this.turn == 'me') {
                    this.turn = 'opp'
                }
                else {
                    this.turn = 'me'
                }
                who = this.turn
            }
            if (who == 'me') {
                this.can_hover_me = true
                this.can_hover_opp = false
                this.can_I_click = true
                this.can_opp_click = false
            }
            else if (who == 'opp') {
                this.can_hover_me = false
                this.can_hover_opp = true
                this.can_I_click = false
                this.can_opp_click = true
                if (this.mode == "online") {
                    this.can_hover_opp = false
                    this.can_opp_click = false
                }
            }
            else if (who == 'none') {
                this.can_hover_me = false
                this.can_hover_opp = false
                this.can_I_click = false
                this.can_opp_click = false
                this.$emit("turn", who)
            }

            if (who == 'me' || who == 'opp') {
                this.$emit("turn", who)
            }
        },
        emit_pts() {
            let opp_pts = 0
            let me_pts = 0
            for (let i = 1; i <= 8; i++) {
                opp_pts += this.pebbles["opp"][i]
                me_pts += this.pebbles["me"][i]
            }
            this.$emit("pts", {
                "opp": opp_pts,
                "me": me_pts
            })
        },
        check_winner() {
            let opp_pts = 0
            let me_pts = 0
            for (let i = 1; i <= 8; i++) {
                opp_pts += this.pebbles["opp"][i]
                me_pts += this.pebbles["me"][i]
            }
            if (opp_pts <= 1) {
                this.winner = 'me'
            }
            else if (me_pts <= 1) {
                this.winner = 'opp'
            }
            else {
                this.winner = 'none'
            }
            return this.winner
        },
        emit_winner() {
            this.$emit('winner', this.winner)
        },
        handle_message(msg) {
            msg = JSON.parse(msg)
            if (msg.type == "move_pebbles") {
                let num = 9 - msg.num
                this.move_pebbles('opp', num)
            }
            else if (msg.type == "finish_turn") {
                if (this.opp_has_finished_turn_animation) {
                    this.make_turn("switch")
                }
                else {
                    this.opp_has_finished_turn_socket = true
                }
            }
        }
    },
    created() {
        for (let i = 1; i <= 8; i++) {
            this.pebbles["opp"][i] = 2
            this.pebbles["me"][i] = 2
            this.hole_pos["opp"][i] = {"top": 0, "left": 0}
            this.hole_pos["me"][i] = {"top": 0, "left": 0}
            this.delt_move["opp"][i] = 2
            this.delt_move["me"][i] = 2
            this.hole_layer_1["opp"][i] = 0
            this.hole_layer_1["me"][i] = 0
        }
        this.emit_pts()

        this.winner = 'none'

        this.make_turn(this.turn)

        if (this.mode == 'online' && this.socket) {
            this.socket.on('message', this.handle_message)
        }
    },
    mounted() {
        this.update_hole_size()
        window.addEventListener("resize", this.update_hole_size)
        this.$emit("turn", this.turn)
    },
    beforeUnmount() {
        window.removeEventListener("resize", this.update_hole_size);
        if (this.timer) {
            clearInterval(this.timer)
        }
        if (this.time_move) {
            clearInterval(this.timer_move)
        }
        if (this.mode == "online" && this.socket) {
            this.socket.off("message", this.handle_message)
        }
    }
}
</script>

<style scoped>

.banner-winner {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    background-color: rgba(128, 0, 0, 0.93);
    color: white;
    z-index: 20;
}
.board-side {
    height: 47%;
    border : 2px solid black;
    border-radius: 30px;
    box-shadow: black 2px 2px 10px;
    background-image: url('~@/assets/img/bg/wood2.jpg');
    background-color: rgb(85, 53, 40);
}
.hole {
    position: relative;
    border-radius: 100%;
    border: 2px solid black;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: ivory;
    box-shadow: inset black 2px 2px 5px;
}

@media (hover: hover) {
    .hole.can_hover_me:hover, .hole.can_hover_opp:hover {
        background-color: black;
        color: white;
        cursor: pointer;
    }
}

.hole.focused_hole {
    /* background-color: black; */
    color: white;
}

.board-row {
    display: flex;
    justify-content: center;
}
</style>