<template>
    <div v-if="mode == 'hostclient'" class="container h-100">
        <div class="banner-top-container w-100">
            <BannerTop :title="title_mode" />
        </div>

        <div class="m-3 d-flex justify-content-center">
            <div class="">
                <button class="btn btn-block btn-dark my-3" @click="mode = 'client'"><i class="fa-solid fa-right-to-bracket"></i> Rejoindre une partie</button>
                <button class="btn btn-block btn-dark my-3" @click="mode = 'host'"><i class="fa-solid fa-circle-plus"></i> Créer une partie</button>
                <router-link to="/" class="btn btn-block btn-dark my-3"><i class="fa-solid fa-rotate-left"></i> Accueil</router-link>
            </div>
        </div>
    </div>

    <div v-else-if="mode == 'host_wait'" class="container h-100">
        <div class="banner-top-container w-100">
            <BannerTop :title="title_mode" />
        </div>

        <div>
            <ul class="m-0">
                <li><strong>Votre pseudo : </strong>{{ player_name }}</li>
                <li><strong>Qui commence : </strong>{{ p_start == 'me' ? "Vous" : "L'adversaire" }}</li>
                <li><strong>Le joueur qui commence utilise : </strong>{{ p_start_c == 'clock' ? 'sens horaire' : 'sens antihoraire' }}</li>
                <li><strong>Vitesse des cailloux : </strong>{{ move_speed_trad }}</li>
                <li v-if="host_wait_state == 'created'"><strong>Code de la partie : </strong>{{game_code}}</li>
            </ul>
            <button v-if="host_wait_state == 'created' && !code_copied" class="btn btn-sm btn-dark my-1" @click="copy_code()">
                <i class="bi bi-clipboard"></i>
                Copier le code
            </button>
            <button v-if="host_wait_state == 'created' && code_copied" class="btn btn-sm btn-dark my-1" :disabled="true">
                <i class="bi bi-clipboard-check"></i>
                Copié
            </button>
        </div>

        <p v-if="host_wait_state == 'wait'" class="text-center mx-0 my-4">Création de la partie en cours</p>
        <p v-else-if="host_wait_state == 'disconnected'" class="text-center mx-0 my-4 text-danger font-italic">Vous êtes déconnecté du serveur</p>

        <div v-if="host_wait_state == 'created'" class="my-2 p-2 border border-dark rounded">
            <p class="m-0 text-center font-weight-bold">En attente de joueur</p>

            <div v-for="(obj, client_sid) in waiting_clients" :key="client_sid" class="my-2 d-flex justify-content-between">
                <p class="text-center m-0">{{ obj.username }}</p>
                <div>
                    <button v-if="!has_accepted_client" class="btn btn-sm btn-outline-dark ml-2" @click="accept_client(client_sid)">Accepter</button>
                    <button v-else-if="client_sid == other_sid" class="btn btn-sm btn-outline-dark ml-2" :disabled="true"><span class="spinner-border spinner-border-sm"></span></button>
                    <button v-else-if="client_sid != other_sid" class="btn btn-sm btn-outline-dark ml-2" :disabled="true">Accepter</button>
                </div>
            </div>
        </div>

        <div class="my-2 d-flex justify-content-center">
            <button class="btn btn-dark" @click="click_host_wait_to_host()">Annuler</button>
        </div>
    </div>

    <div v-else-if="mode == 'client'" class="container h-100">
        <div class="banner-top-container w-100">
            <BannerTop :title="title_mode" />
        </div>

        <h6 class="text-center border-top pt-2">Rejoindre une partie</h6>

        <div class="m-3 d-flex flex-column justify-content-center">
            <div class="d-flex flex-column align-items-center">
                <div class="col-md-4 m-2">
                    <label for="code_input">Saisissez le code</label>
                    <input type="text" class="form-control" id="code_input" autocomplete="off" v-model="client_game_code">
                    <p v-if="error_msg_code" class="text-danger text-center font-italic m-0">{{ error_msg_code }}</p>
                </div>
                <div class="col-md-4 m-2">
                    <label for="player_name">Votre pseudo</label>
                    <input type="text" class="form-control" id="player_name" autocomplete="off" v-model="player_name">
                    <p v-if="error_msg_username" class="text-danger text-center font-italic m-0">{{ error_msg_username }}</p>
                </div>
                <div class="my-3">
                    <button class="btn btn-block btn-dark" @click="click_wait_client()">Confirmer</button>
                    <button class="btn btn-block btn-dark" @click="mode = 'hostclient'">Retour</button>
                </div>
            </div>
        </div>
    </div>

    <div v-else-if="mode == 'client_wait'" class="container h-100">
        <div class="banner-top-container w-100">
            <BannerTop :title="title_mode" />
        </div>

        <div>
            <ul class="m-0">
                <li><strong>Votre pseudo : </strong>{{ player_name }}</li>
                <li><strong>Code de la partie : </strong>{{client_game_code}}</li>
            </ul>
        </div>

        <p v-if="client_wait_state == 'wait'" class="text-center mx-0 my-4">Demande d'entrée dans la partie en cours</p>
        <p v-else-if="client_wait_state == 'joined'" class="text-center mx-0 my-4">Attendez que l'hôte (<span class="font-italic">{{ other_player_name }}</span>) accepte votre demande</p>
        <p v-else-if="client_wait_state == 'no_room'" class="text-center mx-0 my-4 text-danger font-italic">Aucune partie ne correspond au code</p>
        <p v-else-if="client_wait_state == 'host_quit'" class="text-center mx-0 my-4 text-danger font-italic">L'hôte (<span class="font-italic">{{ other_player_name }}</span>) a quitté</p>
        <p v-else-if="client_wait_state == 'disconnected'" class="text-center mx-0 my-4 text-danger font-italic">Vous êtes déconnecté du serveur</p>
        <p v-else-if="client_wait_state == 'host_refuse'" class="text-center mx-0 my-4 text-danger font-italic">L'hôte (<span class="font-italic">{{ other_player_name }}</span>) a refusé votre demande</p>

        <div class="my-2 d-flex justify-content-center">
            <button class="btn btn-dark" @click="click_client_wait_to_client()">Annuler</button>
        </div>
    </div>

    <div v-else-if="mode == 'host'" class="container h-100 d-flex flex-column">
        <div class="banner-top-container w-100">
            <BannerTop :title="title_mode" />
        </div>

        <h6 class="text-center border-top pt-2">Créer une partie</h6>

        <div class="">
            <div class="m-1 p-2 d-flex flex-column align-items-center">
                <div class="col-md-4">
                    <label for="player_name" class="text-center w-100">Votre pseudo</label>
                    <input type="text" class="form-control" id="player_name" autocomplete="off" v-model="player_name">
                    <p v-if="error_msg_username" class="text-danger text-center font-italic m-0">{{ error_msg_username }}</p>
                </div>
            </div>

            <div class="m-1 p-2 d-flex flex-column align-items-center">
                <p class="text-center m-1">Qui commence ?</p>
                <div class="btn-group-vertical" role="group">
                    <button type="button" class="btn btn-sm" :class="{'btn-dark': p_start=='me', 'btn-outline-dark': p_start!='me'}" @click="click_p_start('me')">Vous</button>
                    <button type="button" class="btn btn-sm" :class="{'btn-dark': p_start=='opp', 'btn-outline-dark': p_start!='opp'}" @click="click_p_start('opp')">L'adversaire</button>
                </div>
            </div>

            <div class="m-1 p-2 d-flex flex-column align-items-center">
                <p class="text-center m-1">Le joueur qui commence utilise :</p>
                <div class="btn-group" role="group">
                    <button type="button" class="btn btn-sm" :class="{'btn-dark': p_start_c=='clock', 'btn-outline-dark': p_start_c!='clock'}" @click="click_p_start_c('clock')">
                        <Arrow />
                        <p class="m-0">sens horaire</p>
                    </button>
                    <button type="button" class="btn btn-sm" :class="{'btn-dark': p_start_c=='c-clock', 'btn-outline-dark': p_start_c!='c-clock'}" @click="click_p_start_c('c-clock')">
                        <Arrow :is_clockwise="false" />
                        <p class="m-0">sens antihoraire</p>
                    </button>
                </div>
            </div>

            <div class="m-1 p-2 d-flex flex-column align-items-center">
                <p class="text-center m-1">Vitesse des cailloux</p>
                <div class="btn-group" role="group">
                    <button type="button" class="btn btn-sm" :class="{'btn-dark': move_speed=='slow', 'btn-outline-dark': move_speed!='slow'}" @click="click_speed('slow')">Lente</button>
                    <button type="button" class="btn btn-sm" :class="{'btn-dark': move_speed=='normal', 'btn-outline-dark': move_speed!='normal'}" @click="click_speed('normal')">Moyenne</button>
                    <button type="button" class="btn btn-sm" :class="{'btn-dark': move_speed=='fast', 'btn-outline-dark': move_speed!='fast'}" @click="click_speed('fast')">Rapide</button>
                </div>
            </div>

            <div class="m-3 d-flex flex-column align-items-center">
                <button class="btn btn-sm btn-block btn-outline-dark" @click="click_wait_host()"><i class="fa-solid fa-person-running"></i> Ok</button>
                <button class="btn btn-sm btn-block btn-outline-dark" @click="mode = 'hostclient'"><i class="fa-solid fa-rotate-left"></i> Retour</button>
            </div>
        </div>

        <div></div>
    </div>
    <div v-else-if="mode == 'game'" class="container h-100 d-flex flex-column justify-content-around">
        <div class="banner-top-container w-100">
            <BannerTop :title="title_mode" />
        </div>

        <div class="board-extend-container">
            <div class="row h-100">
                <div class="col h-100">
                    <BoardExtend :speed_timer="speed_timer" :p_start="p_start" :p_start_c="p_start_c" :opp_name="other_player_name" :me_name="player_name" :mode="'online'" :other_sid="other_sid" :socket="socket" :game_code="game_code" @turn="update_turn" :sid="sid" :is_host="i_am_host" />
                </div>
            </div>
        </div>

        <div class="row">
            <div class="col d-flex justify-content-center">
                <ButtonConfig @speed="update_speed" @retry="retry_game" :speed="move_speed" :mode="'online'" :is_host="i_am_host" :turn="whose_turn" :p_start="p_start" :p_start_c="p_start_c" @update_p_c="update_p_start_and_c" />
                <ButtonChat :messages="chat_messages" :opp_name="other_player_name" :my_name="player_name" @chat="send_message" :nb_unseen_notif="chat_messages.length - nb_seen_notif" @open_modal="open_chat_modal" />
            </div>
        </div>

        <div></div>
    </div>
    <div v-else-if="mode == 'restarting'" class="container h-100 d-flex flex-column justify-content-between">
        <div class="banner-top-container w-100">
            <BannerTop :title="title_mode" />
        </div>
        
        <div class="d-flex flex-column align-items-center">
            <span class="spinner-border text-dark"></span>
            <p class="m-3">Recommencement</p>
        </div>

        <div></div>
    </div>
    <div v-else-if="mode == 'restarting_failed'" class="container h-100 d-flex flex-column justify-content-between">
        <div class="banner-top-container w-100">
            <BannerTop :title="title_mode" />
        </div>
        
        <div class="d-flex flex-column align-items-center">
            <p class="m-3">Partie annulée</p>
            <router-link to="/" class="btn btn-block btn-dark my-3">Accueil</router-link>
        </div>

        <div></div>
    </div>
</template>

<script>
import $ from 'jquery'

import BoardExtend from '@/components/game/BoardExtend.vue'
import BannerTop from '@/components/game/BannerTop.vue'
import ButtonConfig from '@/components/game/ButtonConfig.vue'
import ButtonChat from '@/components/game/ButtonChat.vue'
import Arrow from '@/components/game/Arrow.vue'
import io from 'socket.io-client'

export default {
    components: {
        BoardExtend,
        BannerTop,
        ButtonConfig,
        ButtonChat,
        Arrow
    },
    data () {
        return {
            speed_timer: 400,
            // speed_timer: 4000,
            move_speed: 'normal',
            mode: 'hostclient',
            p_start: 'me',
            p_start_c: 'clock',
            title_mode: "Multijoueur en ligne",
            player_name: "Joueur",
            socket: null,
            error_msg_username: "",
            game_code: null,
            client_game_code: "",
            error_msg_code: "",
            waiting_clients: {},
            sid: null,
            client_wait_state: "wait",
            host_wait_state: "wait",
            other_sid: null,
            other_player_name: "Joueur",
            connected_to_server: false,
            code_copied: false,
            timer_copy_code: null,
            has_accepted_client: false,
            i_am_client: false,
            i_am_host: false,
            whose_turn: "none",
            sid_game: null,
            chat_messages: [],
            nb_seen_notif: 0
        }
    },
    computed: {
        move_speed_trad() {
            if (this.move_speed == "normal") {
                return "moyenne"
            }
            if (this.move_speed == "slow") {
                return "lente"
            }
            if (this.move_speed == "fast") {
                return "rapide"
            }
            return ""
        }
    },
    methods: {
        update_p_start_and_c(data) {
            this.p_start = data.p_s
            this.p_start_c = data.p_s_c
        },
        open_chat_modal() {
            this.nb_seen_notif = this.chat_messages.length
        },
        is_chat_scroll_bottom() {
            let modalbodychat_jq = $("#modal-body-chat")
            let modalbodychat = document.getElementById("modal-body-chat")
            let modal_height = modalbodychat_jq.height()
            let is_at_bottom = false
            if (modalbodychat.scrollTop + modal_height == modalbodychat.scrollHeight) {
                is_at_bottom = true
            }
            return is_at_bottom
        },
        make_chat_scroll_bottom() {
            var modalbodychat = document.getElementById("modal-body-chat")
            $("#modal-body-chat").animate({ scrollTop: modalbodychat.scrollHeight }, "slow");
            if (document.querySelectorAll('.modal.show#chat-modal').length > 0) {
                this.nb_seen_notif = this.chat_messages.length
            }
        },
        send_message(data) {
            let new_index = this.chat_messages.length
            this.chat_messages.push({
                "type": "mess",
                "who": "me",
                "sent": false,
                "value": data.message
            })

            this.make_chat_scroll_bottom()
            this.nb_seen_notif = this.chat_messages.length

            this.socket.emit('send_message', JSON.stringify({
                game_code: this.game_code,
                to: this.other_sid,
                from: this.sid_game,
                message: data.message,
                index: new_index,
                "timestamp": new Date().getTime(),
                is_host: this.i_am_host
            }))
        },
        update_turn(who) {
            this.whose_turn = who
        },
        accept_client(sid) {
            this.other_sid = sid
            this.other_player_name = this.waiting_clients[sid]["username"]
            this.has_accepted_client = true
            let data = {
                game_code: this.game_code,
                client_sid: sid,
                who_starts: this.p_start == 'me' ? 'opp' : 'me',
                clock_start: this.p_start_c,
                speed_timer: this.speed_timer,
                client_token: this.waiting_clients[sid]["token"],
                host_token: this.$store.state.token,
                host_name: this.player_name,
                client_name: this.other_player_name
            }
            this.socket.emit('accept_client', JSON.stringify(data))
        },
        make_code_copied_false_in_moment() {
            this.timer_copy_code = setTimeout(function(){
                this.code_copied = false
            }.bind(this), 2000)
        },
        copy_code() {
            this.code_copied = true
            this.make_code_copied_false_in_moment()
            if (!navigator.clipboard) {
                const textArea = document.createElement("textarea");
                textArea.value = this.game_code;
                    
                // Maka textarea opacity 0 so it's not visible
                textArea.style.position = "absolute";
                textArea.style.opacity = 0;
                    
                document.body.prepend(textArea);
                textArea.select();

                try {
                    document.execCommand('copy');
                } catch (error) {
                    console.error(error);
                } finally {
                    textArea.remove();
                }
            }
            else {
                navigator.clipboard.writeText(this.game_code)
            }
        },
        update_speed(speed) {
            this.speed_timer = speed
            let speed_update = 400
            if (speed == 400) {
                speed_update = 'normal'
            }
            else if (speed == 1200) {
                speed_update = 'slow'
            }
            else if (speed == 90) {
                speed_update = 'fast'
            }
            this.move_speed = speed_update
        },
        click_p_start(who) {
            this.p_start = who
        },
        click_p_start_c(clock) {
            this.p_start_c = clock
        },
        start_game() {
            this.mode = 'game'
        },
        retry_game(value) {
            if (value) {
                this.mode = 'restarting'
                let data = {
                    to: this.other_sid,
                    game_code: this.game_code,
                    p_start: this.p_start == "me" ? "opp" : "me",
                    p_start_c: this.p_start_c
                }
                this.socket.emit("restart", JSON.stringify(data))
            }
        },
        click_speed(speed) {
            this.move_speed = speed
            let speed_update = 400
            if (speed == 'normal') {
                speed_update = 400
            }
            else if (speed == 'slow') {
                speed_update = 1200
            }
            else if (speed == 'fast') {
                speed_update = 90
            }
            this.speed_timer = speed_update
        },
        check_player_name() {
            this.player_name = this.player_name.trim()
            this.error_msg_username = ""
            if (!this.player_name || this.player_name.length > 20) {
                this.error_msg_username = "Pseudo entre 1 et 20 caractères"
                return false
            }
            return true
        },  
        check_game_code() {
            this.client_game_code = this.client_game_code.trim().toUpperCase()
            this.error_msg_code = ""
            if (this.client_game_code.length != 6) {
                this.error_msg_code = "Code invalide"
                return false
            }
            return true
        },
        generate_game_code() {
            let result = ''
            const characters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZ'
            const charactersLength = characters.length

            for (let i = 0; i < 6; i++) {
                const randomIndex = Math.floor(Math.random() * charactersLength)
                result += characters.charAt(randomIndex)
            }

            return result
        },
        click_wait_host() {
            if (this.check_player_name()) {
                this.mode = "host_wait"
                this.i_am_client = false
                this.i_am_host = true
                this.waiting_clients = {}
                this.host_wait_state = "wait"
                this.has_accepted_client = false
                if (!this.connected_to_server) {
                    this.host_wait_state = "disconnected"
                }
                this.game_code = this.generate_game_code()
                let data = {
                    token: this.$store.state.token,
                    game_code: this.game_code,
                    create: true,
                    username: this.player_name
                }
                this.socket.emit('join', JSON.stringify(data))
            }
        },
        click_host_wait_to_host() {
            this.mode = "host"
            let data = {}
            this.socket.emit('leave', JSON.stringify(data))
        },
        click_client_wait_to_client() {
            this.mode = "client"
            let data = {}
            this.socket.emit('leave', JSON.stringify(data))
        },
        click_wait_client() {
            if (this.check_player_name() && this.check_game_code()) {
                this.mode = "client_wait"
                this.i_am_client = true
                this.i_am_host = false
                this.client_wait_state = "wait"
                if (!this.connected_to_server) {
                    this.client_wait_state = "disconnected"
                }
                let data = {
                    token: this.$store.state.token,
                    game_code: this.client_game_code,
                    create: false,
                    username: this.player_name
                }
                this.socket.emit('join', JSON.stringify(data))
            }
        },
        handle_message(msg) {
            msg = JSON.parse(msg)
            // console.log(msg)
            if (msg.type == 'on_connect') {
                this.sid = msg.sid
            }
            else if (msg.type == 'response_create_game') {
                if (msg.success) {
                    this.host_wait_state = "created"
                }
            }
            else if (msg.type == 'response_join_game') {
                if (this.mode == 'host_wait') {
                    this.waiting_clients[msg.sid] = {
                        "username": msg.username,
                        "token": msg.token
                    }
                }
                else if (this.mode == 'client_wait') {
                    if (msg.msg == 'no_room') {
                        this.client_wait_state = "no_room"
                    }
                    else if (msg.msg == 'room_exists') {
                        this.other_sid = msg.host_sid
                        this.other_player_name = msg.host_username
                        this.client_wait_state = "joined"
                    }
                }
            }
            else if (msg.type == 'leave_room') {
                if (this.mode == 'host_wait') {
                    delete this.waiting_clients[msg.sid]
                }
                else if (this.mode == 'client_wait') {
                    if (msg.sid == this.other_sid) {
                        this.client_wait_state = "host_quit"
                    }
                }
            }
            else if (msg.type == 'accept_client') {
                this.game_code = this.mode == 'client_wait' ? this.client_game_code : this.game_code
                let data = {
                    "emit_leave_room": false,
                    "game_code": this.game_code
                }
                this.socket.emit('leave', JSON.stringify(data))
                if (this.mode == 'client_wait') {
                    if (msg.sid == this.sid) {
                        this.p_start = msg.who_starts
                        this.p_start_c = msg.clock_start
                        this.speed_timer = msg.speed_timer
                        this.update_speed(this.speed_timer)
                        this.sid_game = msg.sid

                        this.chat_messages = []
                        this.nb_seen_notif = 0

                        this.mode = 'game'
                    }
                    else {
                        this.client_wait_state = "host_refuse"
                    }
                }
                else if (this.mode == 'host_wait') {
                    this.sid_game = msg.host_sid

                    this.chat_messages = []
                    this.nb_seen_notif = 0
                    
                    this.mode = 'game'
                }
            }
            else if (msg.type == "move_pebbles") {
                this.speed_timer = msg.speed
                this.update_speed(this.speed_timer)
            }
            else if (msg.type == "restart_request") {
                this.mode = "restarting"

                this.p_start = msg.p_start
                this.p_start_c = msg.p_start_c
                
                let data = {
                    "game_code": this.game_code,
                    "to": this.other_sid
                }
                this.socket.emit("restart_ok", JSON.stringify(data))
            }
            else if (msg.type == "restart_ok") {
                this.mode = "game"
            }
            else if (msg.type == "restart_no_game") {
                this.mode = "restarting_failed"
            }
            else if (msg.type == 'message_sent') {
                if (this.chat_messages.length > msg.index) {
                    if (this.chat_messages[msg.index].type == "mess" && this.chat_messages[msg.index].who == "me") {
                        this.chat_messages[msg.index].sent = true
                    }
                }
            }
            else if (msg.type == 'receive_message') {
                let is_at_bottom = this.is_chat_scroll_bottom()

                this.chat_messages.push({
                    "type": "mess",
                    "who": "opp",
                    "sent": true,
                    "value": msg.message
                })
                
                if (is_at_bottom) {
                    this.make_chat_scroll_bottom()
                }
            }
            else if (msg.type == 'opp_disconnected') {
                let is_at_bottom = this.is_chat_scroll_bottom()

                this.chat_messages.push({
                    "type": "disco",
                    "who": "opp"
                })
                
                if (is_at_bottom) {
                    this.make_chat_scroll_bottom()
                }
            }
            else if (msg.type == 'opp_reconnected') {
                let is_at_bottom = this.is_chat_scroll_bottom()

                this.chat_messages.push({
                    "type": "reco",
                    "who": "opp"
                })
                
                if (is_at_bottom) {
                    this.make_chat_scroll_bottom()
                }
            }
            else if (msg.type == 'reconnect_no_game') {
                let is_at_bottom = this.is_chat_scroll_bottom()

                this.chat_messages.push({
                    "type": "warn",
                    "value": "La partie n'existe plus. Veuillez créer une nouvelle partie ou en rejoindre une autre."
                })
                
                if (is_at_bottom) {
                    this.make_chat_scroll_bottom()
                }
            }
        },
        handle_disconnect() {
            this.connected_to_server = false
            this.host_wait_state = "disconnected"
            this.client_wait_state = "disconnected"
            if (this.mode == "restarting") {
                this.mode = "hostclient"
            }
            else if (this.mode == "game") {
                let is_at_bottom = this.is_chat_scroll_bottom()
                this.chat_messages.push({
                    "type": "disco",
                    "who": "me"
                })
                
                if (is_at_bottom) {
                    this.make_chat_scroll_bottom()
                }
            }
        },
        handle_connect() {
            this.connected_to_server = true
            // console.log("My sid", this.sid_game)
            if (this.mode == "game") {
                let data = {
                    "game_code": this.game_code,
                    "sid": this.sid_game,
                    "is_host": this.i_am_host
                }
                this.socket.emit("reconnect_game", JSON.stringify(data))

                let is_at_bottom = this.is_chat_scroll_bottom()
                this.chat_messages.push({
                    "type": "reco",
                    "who": "me"
                })
                if (is_at_bottom) {
                    this.make_chat_scroll_bottom()
                }
            }
        }
    },
    mounted() {
        // this.$store.dispatch('set_token', "changed!")
    },
    beforeUnmount() {
        if (this.socket) {
            this.socket.disconnect()
        }
    },  
    created() {
        this.socket = io(this.$store.state.url_b)
        
        this.socket.on('connect', () => {
            // console.log('Connecté au serveur Socket.IO')
            this.handle_connect()
        });

        this.socket.on('disconnect', () => {
            // console.log('Déconnecté du serveur Socket.IO')
            this.handle_disconnect()
        });

        this.socket.on('error', (error) => {
            console.error('Erreur Socket.IO : ', error);
        });

        this.socket.on('message', this.handle_message)
    }
}
</script>

