<template>

  <div v-if="$store.state.token == 'loading'" class="loading-screen-home h-100 d-flex justify-content-center align-items-center">
    <div class="">
      <div class="">
        <KatroTitle />
      </div>
      <div class="d-flex justify-content-center p-3">
        <span class="spinner-border text-dark"></span>
      </div>
      <div class="text-center text-dark">Chargement</div>
    </div>
  </div>


  <div v-else id="theapp" class="h-100">
    <router-view :key="$route.fullPath" />
  </div>

</template>

<script>
import axios from 'axios'
import KatroTitle from '@/components/home/KatroTitle.vue'

export default {
  name: 'App',
  components: {
    KatroTitle
  },
  methods: {
    update_token (fullpath, name, params, query) {
      axios
        .get(this.$store.state.url_b+"/api/token", {
          params: {
            "token": this.$store.state.token || "loading",
            "fullpath": fullpath,
            "name": name,
            "params": params,
            "query": query
          }
        })
        .then(res => {
          this.$store.dispatch('set_token', res.data.token)
        })
        .catch(error => {
          console.log(error)
          this.$store.dispatch('set_token', "error")
        })
        .finally(() => {
            
        })
    }
  },
  created() {
    this.$store.dispatch('set_is_in_game', false)
    // Confirm if really quiting the game
    this.$router.beforeEach((to, from, next) => {
      if (this.$store.state.is_in_game) {
        const answer = window.confirm('Voulez-vous vraiment quitter cette page ?')
        if (answer) {
          next()
        }
        else {
          next(false)
        }
      }
      else {
        next()
      }
    })
  },
  watch: {
    $route (to) {
      this.update_token(to.fullPath, to.name, to.params, to.query)
    },
  }
}
</script>

<style>
html, body, #app {
  height: 100%;
}

#theapp {
  overflow: auto;
}

@font-face {
  font-family: "AcmeRegular";
  src: local("AcmeRegular"),
  url(./fonts/Acme-Regular.ttf) format("truetype");
}

.font-1 {
  font-family: "AcmeRegular", Helvetica, Arial;
}
</style>
