        </div>
        <div v-else-if="v==7" class="pebbles-config">
            <Pebble :top="20" :left="20"  />
            <Pebble :top="10" :left="50"  />
            <Pebble :top="50" :left="10"  />
            <Pebble :top="60" :left="60"  />
            <Pebble :top="30" :left="70"  />
            <Pebble :top="70" :left="30"  />
            <Pebble :top="40" :left="40"  />
        </div>
        <div v-else-if="v==8" class="pebbles-config">
            <Pebble :top="32" :left="32"  />
