        </div>
        <div v-else-if="v>=10" class="pebbles-config">
            <Pebble v-for="i in v" :key="i" :top="10+(Math.floor((i-1)/4)*20)" :left="10+(20 * ((i-1)%4))"  />
        </div>
        <div v-else class="pebbles-config">
            <!-- {{ v }} ; {{ hole_size }} -->
            <!-- <Pebble :top="pos_pebble(30, hole_size)" :left="pos_pebble(30, hole_size)"  /> -->
