<script>
import Pebble from '@/components/game/Pebble.vue'

export default {
    components: {
        Pebble
    },
    props: {
        top: {
            type: Number,
            default: 0
        },
        left: {
            type: Number,
            default: 0
        },
        size: {
            type: Number,
            default: 100
        },
        bgcolor: {
            type: String,
            default: ''
        },
        zindex: {
            type: Number,
            default: 0
        },
        v: {
            type: Number,
            default: 0
        },
        layer: {
            type: Number,
            default: 0
        },
        hole_size: {
            type: String,
            default: '0px'
        }
    },
    data () {
        return {
        }
    },
    methods: {
        resize_pebble(hole_size) {
            let size = parseFloat(hole_size.replace('px', ''))
            let resize = size / 6.5
            return resize + 'px'
        },
        pos_pebble(px, hole_size) {
            let size = parseFloat(hole_size.replace('px', ''))
            let new_px = size * px / 83.5
            return new_px
        },
    },
    mounted() {

    }
}
</script>

