<template>
    <button class="btn btn-sm btn-info mx-2" data-toggle="modal" data-target="#chat-modal" ref="btn_chat" @click="open_chat()">
        <i class="bi bi-chat-fill"></i>
        Chat
        <span v-if="nb_unseen_notif > 0" class="badge badge-danger">{{nb_unseen_notif}}</span>
    </button>

    <!-- Modal -->
    <div class="modal fade" id="chat-modal" tabindex="-1" role="dialog" aria-labelledby="chat-modal-title" aria-hidden="true" ref="chat_modal">
        <div class="modal-dialog modal-dialog-centered modal-lg modal-dialog-scrollable" role="document" id="chat-modal-dialog">
            <div class="modal-content">
                <div class="modal-header">
                    <h5 class="modal-title">Chat</h5>
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                        <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div class="modal-body p-0" id="modal-body-chat">
                    <div class="p-1 content-notification">
                        <div v-if="messages.length == 0" class="">
                            <p class="m-5 text-center font-italic">
                                Le vide intersidéral...
                            </p>
                        </div>

                        <div v-for="(m, i) in messages" :key="i">
                            <div v-if="m.type == 'mess'" :class="{'message-not-sent': m.who == 'me' && !m.sent}">
                                <span :class="{'text-info': m.who == 'me'}">
                                    <strong>{{m.who == "me" ? my_name + " (Vous)" : opp_name}} : </strong>
                                </span>
                                <span class="">{{ m.value }}</span>
                            </div>

                            <div v-else-if="m.type == 'disco'" class="d-flex justify-content-center">
                                <span class="m-2 font-italic text-danger text-center">
                                    {{m.who == "me" ? "Vous vous êtes déconnecté" : opp_name + " s'est déconnecté"}}
                                </span>
                            </div>

                            <div v-else-if="m.type == 'reco'" class="d-flex justify-content-center">
                                <span class="m-2 font-italic text-success text-center">
                                    {{m.who == "me" ? "Vous vous êtes reconnecté" : opp_name + " s'est reconnecté"}}
                                </span>
                            </div>

                            <div v-else-if="m.type == 'warn'" class="d-flex justify-content-center bg-dark">
                                <span class="m-2 font-italic text-warning text-center">
                                    {{m.value}}
                                </span>
                            </div>
                        </div>
                    </div>
                </div>

                <div class="modal-footer bg-info p-0">
                    <div class="w-100">
                        <div class="input-group input-group-sm">
                            <input type="text" class="form-control" placeholder="Votre message..." autocomplete="off" v-model="message_to_send" @keyup.enter="send_message()">
                            <button class="btn btn-sm btn-dark" type="button" @click="send_message()">
                                <i class="fa-solid fa-rocket"></i>
                            </button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import $ from 'jquery'

export default {
    components: {

    },
    props: {
        messages: {
            default: []
        },
        opp_name: {
            type: String,
            default: "Joueur"
        },
        my_name: {
            type: String,
            default: "Joueur"
        },
        nb_unseen_notif: {
            type: Number,
            default: 0
        }
    },
    emits: ["chat", "open_modal"],
    data () {
        return {
            message_to_send: ''
        }
    },
    computed: {
    },
    methods: {
        send_message() {
            let message = this.message_to_send.trim()
            this.message_to_send = message
            if (message) {
                if (message.length <= 1000) {
                    this.message_to_send = ''
                    let data = {
                        "message": message
                    }
                    this.$emit('chat', data)
                }
                else {
                    alert("Message trop long. 1000 caractères max.")
                }
            }
        },
        make_scroll_bottom() {
            // $("#modal-body-chat").animate({ scrollTop: $("#modal-body-chat").height() }, "slow");

            var modalbodychat = document.getElementById("modal-body-chat")

            setTimeout(function() {
                // modalbodychat.scrollTop = modalbodychat.scrollHeight
                let new_scroll_top = modalbodychat.scrollHeight
                if (new_scroll_top == 0) {
                    new_scroll_top = 999999999
                }
                $("#modal-body-chat").animate({ scrollTop:  new_scroll_top }, "fast");
            }, 300)
        },
        open_chat() {
            this.make_scroll_bottom()
            this.$emit("open_modal")
        },
        close_modal() {
            if (document.querySelectorAll('.modal.show#chat-modal').length > 0) {
                this.$refs.btn_chat.click()
            }
        },
        before_window_unload(e) {
            e.preventDefault()
            let return_value = "Vous êtes sur le point de quitter cette partie"
            e.returnValue = return_value
            return return_value
        }
    },
    created() {
        window.addEventListener('beforeunload', this.before_window_unload)
    },
    mounted() {
        $("#chat-modal").on("shown.bs.modal", function(e) {
            console.log("Alefa barea", e)
        })
    },
    beforeUnmount() {
        window.removeEventListener('beforeunload', this.before_window_unload)
        this.close_modal()
    }
}
</script>

<style scoped>
.message-not-sent {
    opacity: 0.4;
}
</style>